/* General Styles */
body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background: linear-gradient(to bottom, #ffd1dc, #ffe6e6); /* Pastel pink gradient */
  color: #333;
  text-align: center;
}

/* Header Styles */
.header {
  background-color: #ffccd5; /* Pastel pink */
  color: #333;
  padding: 20px 0;
  font-size: 2.5em;
  border-bottom: 3px solid #ffa6c1;
}

/* Navigation Styles */
.nav {
  background-color: #ffe6e6; /* Lighter pastel pink */
  padding: 10px 0;
}

.nav a {
  color: #ff6f91; /* Pink accent */
  text-decoration: none;
  margin: 0 15px;
  font-size: 1.2em;
  font-weight: bold;
}

.nav a:hover {
  text-decoration: underline;
  color: #ff4081;
}

/* Section Styles */
.section {
  margin: 30px auto;
  padding: 20px;
  max-width: 800px;
  background-color: #ffffff;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
}

/* Gallery Images */
/* Gallery Images */
.section img {
  width: 200px; /* Set a consistent width */
  height: 200px; /* Set a consistent height */
  object-fit: cover; /* Crop images to fill the dimensions while maintaining the aspect ratio */
  margin: 10px; /* Add spacing between images */
  border-radius: 8px; /* Optional: Add rounded corners */
}



/* Footer Styles */
.footer {
  background-color: #ffccd5; /* Pastel pink */
  color: #333;
  padding: 10px 0;
  position: fixed;
  bottom: 0;
  width: 100%;
  font-size: 1.1em;
  border-top: 3px solid #ffa6c1;
}

/* Typography */
h2 {
  color: #ff6f91;
}

ul {
  list-style-type: square;
  padding-left: 20px;
  text-align: left;
}

li {
  margin-bottom: 10px;
}

/* Responsive Design */
@media (max-width: 768px) {
  .section {
      padding: 15px;
  }

  .nav a {
      font-size: 1em;
      margin: 0 10px;
  }

  .header {
      font-size: 2em;
  }
}
